<template>
  <div class="modal">
    <div class="modal-content">
      <a-form-model
        ref="form"
        :model="entity"
        autocomplete="off"
        layout="vertical"
      >
        <a-row :gutter="24">
          <a-col :span="6">
            <a-form-model-item
              label="设备"
              prop="rescues"
              :rules="{
                required: true,
                message: '请选择设备',
                trigger: 'change',
              }"
            >
              <g-select
                v-model="entity.rescues"
                loadType="scroll"
                :pagination="{ pageSize: 10 }"
              />
            </a-form-model-item>
          </a-col>

          <a-col :span="6">
            <a-form-model-item
              label="设备"
             
            >
              <g-rdo-select
                v-model="entity.rescued"
                :showSearch="true"
                loadType="scroll"
                :pagination="{ pageSize: 10 }"
              />
            </a-form-model-item>
          </a-col>

          <a-col :span="6">
            <a-form-model-item
              label="设备"
              
            >
              <g-select
                v-model="entity.rescues"
                :showSearch="true"
                loadType="scroll"
                :pagination="{ pageSize: 10 }"
              />
            </a-form-model-item>
          </a-col>

          <a-col :span="6">
            <a-form-model-item
              label="设备"
              
            >
              <g-select
                v-model="entity.rescued"
                :showSearch="true"
                loadType="scroll"
                :pagination="{ pageSize: 10 }"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </a-form-model>
    </div>
    <div class="modal-footer">
      <g-btn text="确认" @callback="submit" type="primary"></g-btn>

      <g-btn text="取消" @callback="close" ></g-btn>
    </div>
  </div>
</template>
<script>
import { defineComponent } from "vue";
export default defineComponent({
  props: {
    title: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      entity: {
        rescues: undefined,
        rescued: undefined
      },
    };
  },

  methods: {
    close() {
      this.$emit("closeModal");
    },
    submit() {
      // 校验
      this.$refs["form"].validate().then((valid) => {
        if (!valid) {
          return;
        }
        this.$message.success("提交成功");
        this.close();
      });
    },
  },
});
</script>
