<template>
    <div class="flex-user">
        <div
            style="
                background-color: #fff;
                margin-bottom: 10px;
                padding: 12px 24px;
                box-sizing: border-box;
                border-radius: 4px;
            "
        >
            <a-form :form="form" layout="vertical">
                <a-row :gutter="24">
                    <a-col :span="4">
                        <a-form-item label="用户">
                            <a-input
                                placeholder="请输入用户名"
                                v-model="form.userName"
                            ></a-input>
                        </a-form-item>
                    </a-col>
                    <a-col :span="4">
                        <a-form-item label="用户">
                            <a-input
                                placeholder="请输入用户名"
                                v-model="form.userName"
                            ></a-input>
                        </a-form-item>
                    </a-col>
                    <a-col :span="4">
                        <a-form-item label="用户">
                            <a-input
                                placeholder="请输入用户名"
                                v-model="form.userName"
                            ></a-input>
                        </a-form-item>
                    </a-col>
                    <a-col :span="4">
                        <a-form-item label="用户">
                            <a-input
                                placeholder="请输入用户名"
                                v-model="form.userName"
                            ></a-input>
                        </a-form-item>
                    </a-col>
                    <a-col :span="4">
                        <a-form-item label="用户">
                            <a-input
                                placeholder="请输入用户名"
                                v-model="form.userName"
                            ></a-input>
                        </a-form-item>
                    </a-col>
                    <a-col :span="4">
                        <a-form-item :label="'\u00a0'">
                            <g-btn
                                text="查询"
                                class="mr-2"
                                type="primary"
                            ></g-btn>
                            <g-btn text="重置"></g-btn>
                        </a-form-item>
                    </a-col>
                </a-row>
            </a-form>
        </div>

        <div
            style="
                background-color: #fff;
                padding: 12px 24px 0;
                box-sizing: border-box;
                overflow-y: hidden;
                border-radius: 4px;
            "
        >
            <!-- <div
        style="display: flex; justify-content: flex-end; margin-bottom: 10px"
      >
        
      </div> -->
        <g-table-bar :spacing.sync="tableSize" :columns.sync="columns" :virtualColumns="virtualColumns" @refreshTable="refresh">
                <g-btn
                    text="添加"
                    style="margin-right: 10px"
                    @callback="addOrEditSpec"
                ></g-btn>
                <g-btn
                    text="批量删除"
                    type="danger"
                    style="margin-right: 10px"
                ></g-btn>
                <g-btn text="导出excel" style="margin-right: 10px"></g-btn>
            </g-table-bar>
            <a-table
                id="tables"
                :columns="columns"
                :data-source="dataList"
                :scroll="{ y: tableheight }"
                :row-key="(record) => record.id"
                :pagination="pagination"
                :rowSelection="rowSelection"
                @change="tableChange"
                :loading="isTableLoading"
                :size="tableSize"
            >
                <!-- 角色 -->
                <span slot="role" slot-scope="text">
                    <a-tag color="blue">{{ text }}</a-tag>
                </span>
                <!-- 操作 -->
                <span slot="action" slot-scope="text, record">
                    <a
                        href="javascript:;"
                        class="mr-1"
                        @click="addOrEditSpec(record)"
                        >编辑</a
                    >
                    <a href="javascript:;" class="del" @click="delSpec(record)"
                        >删除</a
                    >
                </span>
            </a-table>
        </div>
    </div>
</template>

<script>
import { getTableScroll } from "@/utils/getTableScroll";
import createModal from "@/utils/modal";
import CreateOrEditSpecConfig from "./create-or-edit-spec-config/create-or-edit-spec-config.vue";
import { tableBase } from "@/utils";
export default {
    name: "spec-config",
    mixins: [tableBase],
    data() {
        return {
            tableheight: 0,
            form: {
                userName: "",
            },
        };
    },

    mounted() {
       

        this.columns = [
            // 序号 index
            {
                title: "序号",
                dataIndex: "id",
                key: "id",
                customRender: (text, record, index) => `${index + 1}`,
                width: 80,
            },
            {
                title: "角色",
                dataIndex: "role",
                key: "role",
                scopedSlots: { customRender: "role" },
            },
            {
                title: "用户邮箱",
                dataIndex: "email",
                key: "email",
                ellipsis: true,
            },
            {
                title: "用户名",
                dataIndex: "name",
                key: "name",
            },

            {
                title: "创建时间",
                dataIndex: "createTime",
                key: "createTime",
                // 排序
                sorter: true,
                ellipsis: true,
                width: 200,
            },

            {
                title: "操作",
                dataIndex: "action",
                key: "action",
                scopedSlots: { customRender: "action" },
            },
        ];
			//this.columns数组深拷贝this.virtualColumns
			this.virtualColumns = JSON.parse(JSON.stringify(this.columns)).map(
				(item) => {
					item.checked = true;
					return item;
				}
			)
			this.$nextTick(() => {
            this.tableheight = getTableScroll({ id: "tables" });
        });
		
    },

    methods: {
        addOrEditSpec(record) {
            createModal(
                {
                    title: record ? "编辑工艺配置" : "添加工艺配置",
                    width: "800px",
                },
                { component: CreateOrEditSpecConfig }
            );
        },

        fetchData(request) {
            // 手动循环生成数据 200条
            this.isTableLoading = true;
            setTimeout(() => {
                var _data = [];
                for (let i = 0; i < 200; i++) {
                    _data[i] = {
                        id: i,
                        role: "管理员" + i,
                        email: "123@qq.com",
                        name: "admin",
                        createTime: "2022-01-01 00:00:00",
                    };
                }
                request.finishedCallback && request.finishedCallback();
                request.successCallback &&
                    request.successCallback({ items: _data, totalCount: 200 });
            }, 200);
        },

        delSpec(record) {
            // 删除 延时
            this.$confirm({
                title: "确认删除",
                content: "确认删除吗?",
                onOk: () => {
                    this.isTableLoading = true;
                    setTimeout(() => {
                        // 刷新
                        this.refresh();
                    }, 500);
                },
            });
        },
    },
};
</script>

<style lang="scss" scoped>
.flex-user {
    box-sizing: border-box;
}
</style>
